export class FileStorageClient {
    constructor() {
        this.baseUrl = 'https://localhost:55693';
        this.apiPath = '/api/files';
    }

    /**
     *   URL  
     * @param {string} endpoint 
     * @returns {string}
     */
    _GetUrl(endpoint) {
        return `${this.baseUrl}${this.apiPath}${endpoint}`;
    }

    /**
     *    
     * @param {File} file -   
     * @returns {Promise<Object>}     URL
     * @property {number} id - ID 
     * @property {string} fileUrl - URL   
     * @property {string} downloadUrl - URL   
     * @example
     * const result = await client.uploadFile(file);
     * console.log('  :', result.fileUrl);
     * document.getElementById('preview').src = result.fileUrl;
     */
    async UploadFile(file) {
        if (!(file instanceof File)) {
            throw new Error('    File');
        }

        const formData = new FormData();
        formData.append('file', file);

        try {
            const response = await fetch(this._GetUrl('/upload'), {
                method: 'POST',
                body: formData
            });

            if (!response.ok) {
                const error = await response.text();
                throw new Error(` : ${error}`);
            }

            return await response.json();
        } catch (error) {
            console.error('   :', error);
            throw error;
        }
    }

    /**
     *  URL     
     * @param {string} bucketName -  
     * @param {string} fileName -  
     * @returns {string} URL  
     * @example
     * const url = client.getFileViewUrl('documents', 'abc123_document.pdf');
     * document.getElementById('preview').src = url;
     */
    GetFileViewUrl(bucketName, fileName) {
        return this._GetUrl(`/view/${bucketName}/${fileName}`);
    }

    /**
     *  URL     ID
     * @param {number} id - ID 
     * @returns {string} URL  
     * @example
     * const url = client.getFileDownloadUrl(123);
     * window.open(url, '_blank');
     */
    GetFileDownloadUrl(id) {
        return this._GetUrl(`/download/${id}`);
    }

    /**
     *     ID
     * @param {number} id - ID 
     * @returns {Promise<Object>}  
     * @example
     * const metadata = await client.getFileMetadata(123);
     * console.log(' :', metadata.fileSize);
     */
    async GetFileMetadata(id) {
        try {
            const response = await fetch(this._GetUrl(`/${id}`), {
                method: 'GET',
                headers: {
                    'Content-Type': 'application/json'
                }
            });

            if (!response.ok) {
                const error = await response.text();
                throw new Error(`  : ${error}`);
            }

            return await response.json();
        } catch (error) {
            console.error('   :', error);
            throw error;
        }
    }

    /**
     *    ID
     * @param {number} id - ID 
     * @param {string} saveAs -     (,    )
     * @returns {Promise<void>}
     * @example
     * await client.downloadFile(123, 'my-document.pdf');
     */
    async DownloadFile(id, saveAs = null) {
        try {
            const response = await fetch(this._GetUrl(`/download/${id}`), {
                method: 'GET'
            });

            if (!response.ok) {
                const error = await response.text();
                throw new Error(` : ${error}`);
            }

            const blob = await response.blob();
            
            //        
            let fileName = saveAs;
            if (!fileName) {
                const contentDisposition = response.headers.get('Content-Disposition');
                if (contentDisposition) {
                    const matches = /filename[^;=\n]*=((['"]).*?\2|[^;\n]*)/.exec(contentDisposition);
                    if (matches && matches[1]) {
                        fileName = matches[1].replace(/['"]/g, '');
                    }
                }
            }
            fileName = fileName || `file-${id}`;

            //    
            const url = URL.createObjectURL(blob);
            const a = document.createElement('a');
            a.style.display = 'none';
            a.href = url;
            a.download = fileName;
            document.body.appendChild(a);
            a.click();
            
            // 
            URL.revokeObjectURL(url);
            document.body.removeChild(a);
        } catch (error) {
            console.error('   :', error);
            throw error;
        }
    }

    /**
     *  Blob  (  )
     *        
     * @param {number} id - ID 
     * @returns {Promise<Blob>}
     * @example
     * const blob = await client.getFileBlob(123);
     * const url = URL.createObjectURL(blob);
     * document.getElementById('preview').src = url;
     */
    async GetFileBlob(id) {
        try {
            const response = await fetch(this._GetUrl(`/download/${id}`), {
                method: 'GET'
            });

            if (!response.ok) {
                const error = await response.text();
                throw new Error(`  : ${error}`);
            }

            return await response.blob();
        } catch (error) {
            console.error('   blob:', error);
            throw error;
        }
    }

    /**
     *  URL   
     * @param {number} id - ID 
     * @returns {Promise<string>} Object URL    src
     * @example
     * const url = await client.getFilePreviewUrl(123);
     * document.getElementById('image').src = url;
     * //    URL.revokeObjectURL(url)  URL   
     */
    async GetFilePreviewUrl(id) {
        const blob = await this.getFileBlob(id);
        return URL.createObjectURL(blob);
    }

    /**
     *    ID
     * @param {number} id - ID 
     * @returns {Promise<boolean>}
     * @example
     * await client.deleteFile(123);
     * console.log(' ');
     */
    async DeleteFile(id) {
        try {
            const response = await fetch(this._GetUrl(`/${id}`), {
                method: 'DELETE'
            });

            if (!response.ok) {
                const error = await response.text();
                throw new Error(` : ${error}`);
            }

            return true;
        } catch (error) {
            console.error('   :', error);
            throw error;
        }
    }

    /**
     *    
     * @param {File[]} files -  
     * @returns {Promise<Object[]>}   
     * @example
     * const results = await client.uploadMultipleFiles(filesArray);
     * const successful = results.filter(r => r.success);
     * console.log(` ${successful.length}  ${results.length}`);
     */
    async UploadMultipleFiles(files) {
        const results = [];

        for (const file of files) {
            try {
                const result = await this.uploadFile(file);
                results.push({ 
                    success: true, 
                    fileName: file.name, 
                    data: result 
                });
            } catch (error) {
                results.push({ 
                    success: false, 
                    fileName: file.name, 
                    error: error.message 
                });
            }
        }

        return results;
    }

    /**
     *     (,     )
     * @param {File[]} files -  
     * @returns {Promise<Object[]>}   
     * @example
     * const results = await client.uploadMultipleFilesParallel(filesArray);
     */
    async UploadMultipleFilesParallel(files) {
        const uploadPromises = files.map(async (file) => {
            try {
                const result = await this.uploadFile(file);
                return { 
                    success: true, 
                    fileName: file.name, 
                    data: result 
                };
            } catch (error) {
                return { 
                    success: false, 
                    fileName: file.name, 
                    error: error.message 
                };
            }
        });

        return await Promise.all(uploadPromises);
    }

    /**
     *   URL
     * @param {string} newBaseUrl 
     */
    SetBaseUrl(newBaseUrl) {
        this.baseUrl = newBaseUrl.endsWith('/') ? newBaseUrl.slice(0, -1) : newBaseUrl;
    }

    /**
     *    URL
     * @returns {string}
     */
    GetBaseUrl() {
        return this.baseUrl;
    }
}

document.addEventListener('authStateChanged', async () => {    
    const { isAuthenticated, userData } = event.detail;

    if (isAuthenticated && userData) {
        const FileStorage = new FileStorageClient();
    }
});

